.keys-list{
    list-style: none;
    margin: 20px auto;
    /* max-width: 500px; */
    padding: 0;
}
.keys-list li{
    display: inline-block;
    margin-right: 20px;
    border: 1px solid;
    color: #333;
    font-size: 12px;
    border-radius: 13px;
    line-height: 1;
    padding: 6px 13px;
}


